<template>
    <div class="list_box">
        <div class="searchBox">
            <h3><span @click='goLast' class="icon-left"></span>
                <p>今日详单</p>
            </h3>
            <div class="searchInput">
                <div class="search_row">
                    <input class="my-search" v-model="value1" placeholder="输入人员/称谓查询" />
                    <i @click="submitHandler" class="icon-search"></i>
                    <div class="menuBox" align="right">
                        <i class="icon-menu"></i>
                    </div>
                </div>
            </div>
        </div>
        <div class="info_list">
            <pull-to @infinite-scroll="getMoreList" class="file-lists">
                <ul class="pull_ul">
                    <li v-for="item in dataList">
                        <p class="first">{{item.name}}</p>
                        <div class="second">
                            <span style="margin-right:1rem;">单&#12288;量：{{item.num}}</span>
                            <span>合计收益：{{item.money}}元</span>
                        </div>
                        <p class="third">时&#12288;间：{{item.time}}</p>
                        <div class="more" :class="{'on':item.haslist}" @click="getNextList(item)">
                            <span class="icon-right"></span>
                        </div>
                    </li>
                </ul>
            </pull-to>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                value1: "",
                dataList: []
            }
        },
        methods: {
            goLast() {
                this.$router.go(-1);
            },
            submitHandler() {
                console.log(123);
            },
            getMoreList() {
                console.log("翻页");
            },
            getNextList(item) {
                this.$router.push({
                    path: "todayIncomeDetail",
                    query: item.id
                })
            }
        },
        created(){
            console.log(this.$route.query.id);
        }
    }
</script>


